/* Import Spotify font */
@import url('https://fonts.googleapis.com/css2?family=Spotify&display=swap');

/* Define Spotify green color */
:root {
  --spotify-green: #1DB954;
}

/* Body styles */
body {
  font-family: 'Spotify', sans-serif;
  background-color: #000;
  color: #fff;
  margin: 0;
  padding: 0;
}

/* Navbar styles */
.navbar {
  display: flex;
  justify-content: flex-end;
  padding: 20px;
  position: sticky;
  top: 0;
  background-color: #000;
}

.navbar a {
  color: #fff;
  text-decoration: none;
  margin-right: 20px;
  font-size: 1.2rem;
  transition: color 0.3s ease;
}

.navbar a:hover {
  color: var(--spotify-green);
}

/* Container styles */
.container {
  text-align: center;
  padding: 50px;
}

/* Heading styles */
h1 {
  font-size: 3rem;
  margin-bottom: 20px;
}

/* Paragraph styles */
p {
  font-size: 1.5rem;
  margin-bottom: 20px;
}

/* List styles */
ul {
  list-style-type: none;
  padding: 0;
}

/* Project styles */
.projects {
  margin-top: 50px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.project {
  background-color: #fff;
  color: #1DB954;
  border-radius: 10px;
  padding: 20px;
  margin: 20px;
  width: 300px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.project:hover {
  transform: translateY(-5px);
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
}
